import React from "react";
import Animate from "grommet/components/Animate";
import Box from "grommet/components/Box";

export default class Android extends React.Component {
  constructor(){
    super();
    this.state = {
      color: undefined
    }
  }
  componentDidMount(){
    let colors = this.props.setColorOfContent();
    this.setState({
      color: colors.android
    });
  }
  render() {
    let style = {
      backgroundColor: this.state.color + "1a"
    }
    return (
        <Animate
          enter={{ animation: "slide-right", duration: 300, delay: 0 }}
          keep={true}
        >
        <Box className="content-box android" style={style}>
          <h1 className="content-title">Projects</h1>
          <p>A long time ago in a galaxy far far away...</p>
          <ul>
            <li><h3><a target="_blank" href="https://play.google.com/store/apps/details?id=org.selak.narodnidaine">Life Toss</a></h3><p>Coin flip app with smart quotes<br /><a target="_blank" href="https://www.dropbox.com/s/p7il0a78jypajnl/Alkotest.zip?dl=0">Source Code</a> - <a target="_blank" href="https://play.google.com/store/apps/details?id=org.selak.narodnidaine">Google Play</a><br />Built With: Java,Xml</p></li>
            <li><h3><a target="_blank" href="https://play.google.com/store/apps/details?id=org.selak.alkotest">
            Alkotest</a></h3><p>Rating and quotes game<br /><a target="_blank" href="https://www.dropbox.com/s/p7il0a78jypajnl/Alkotest.zip?dl=0">Source Code</a> - <a target="_blank" href="https://play.google.com/store/apps/details?id=org.selak.alkotest">Google Play</a><br />Built With: Java,Xml,Css</p></li>
            <li><h3><a target="_blank" href="https://play.google.com/store/apps/details?id=org.selak.virtualnakocka">
            Virtualna Kocka</a></h3><p>Dice throwing game<br /><a target="_blank" href="https://www.dropbox.com/s/nonyppgbf2c15lh/VirtualnaKocka.zip?dl=0">Source Code</a> - <a target="_blank" href="https://play.google.com/store/apps/details?id=org.selak.virtualnakocka">Google Play</a><br />Built With: Java,Xml</p></li>
            <li><h3><a target="_blank" href="https://play.google.com/store/apps/details?id=org.selak.kalkulator">
            Digitron</a></h3><p>Calculator<br /><a target="_blank" href="https://www.dropbox.com/s/8ozf1bgvnqvcbxm/Kalkulator.zip?dl=0">Source Code</a> - <a target="_blank" href="https://play.google.com/store/apps/details?id=org.selak.kalkulator">Google Play</a><br />Built With: Java,Xml</p></li>
          </ul>
          </Box>
        </Animate>
    );
  }
}



